body {
    font-family: Arial, sans-serif;
    font-size: 16px;
    color: #333;
    line-height: 1.5;
    margin: 10;
    padding: 0;
  }
  
  header {
    background-color: #333;
    color: #fff;
    padding: 10px;
  }
  
  nav ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }
  
  nav ul li {
    margin-right: 10px;
  }
  
  nav ul li a {
    color: #fff;
    text-decoration: none;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .form label {
    font-weight: bold;
  }
  
  .form input {
    margin-bottom: 10px;
    padding: 5px;
    border: 1px solid #333232;
    border-radius: 4px;
    width: 100%;
  }
  
  .form textarea {
    margin-bottom: 10px;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 3px;
    width: 100%;
  }
  
  .form button {
    margin-top: 10px;
    padding: 10px 20px;
    background-color: #333;
    color: #fff;
    border: none;
    border-radius: 3px;
    cursor: pointer;
  }
  
  .preview {
    margin-top: 30px;
  }
  
  .preview h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .preview h3 {
    font-size: 20px;
    margin-bottom: 5px;
  }
  
  .preview h4 {
    font-size: 18px;
    margin-top: 20px;
    margin-bottom: 5px;
  }
  
  .preview p {
    margin: 0;
  }
  
  .download {
    margin-top: 20px;
    display: flex;
    justify-content: left;
  }
  
  .download button {
    margin-right: 10px;
    padding: 10px 20px;
    background-color: #333;
    color: #fff;
    border: none;
    border-radius: 3px;
    cursor: pointer;
  }
  



  .side {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width: 200px;
    background-color: #f5f5f5;
    box-shadow: 1px 0 5px rgba(0, 0, 0, 0.2);
  }
  
  .side__header {
    height: 50px;
    background-color: #e0e0e0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 1.2rem;
  }
  
  .side__content {
    padding: 20px;
  }
  
  .side__content h2 {
    font-size: 1.1rem;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .side__content p {
    font-size: 1rem;
    line-height: 1.5;
    margin-bottom: 10px;
  }
  